body{background-color:#fcfcfc;font-family:helvetica, arial, sans-serif}.main .header{background-color:#0D0912;color:#fcfcfc}.main .header .page-header h1.title{font-weight:600}.main .header .page-header .subtitle{font-size:24px;font-weight:100}.main .header .page-header .btn-cta-on,.main .header .page-header .btn-cta-off{border:2px solid;margin:auto}.main .header .page-header .btn-cta-on{color:#FFFFFF;background-color:#3AFFA1;border:2px solid #3AFFA1}.main .header .page-header .btn-cta-on:hover{color:#FFFFFF;background-color:transparent;border:2px solid #3AFFA1}.main .header .page-header .btn-cta-off{color:#FFFFFF;background-color:transparent;border:2px solid #3AFFA1}.main .header .page-header .btn-cta-off:hover{color:#FFFFFF;background-color:#3AFFA1;border:2px solid #3AFFA1}.main .footer{background-color:#0D0912;color:#fcfcfc}nav.navbar{background-color:#0D0912;margin-bottom:0}nav.navbar .navbar-brand{background-color:#FB1D5E}nav.navbar ul.navbar-nav{color:#ffffff}nav.navbar ul.navbar-nav li{color:#ffffff;font-size:22px;font-weight:100}nav.navbar ul.navbar-nav li a{background-color:transparent;color:#ffffff}nav.navbar ul.navbar-nav li a:hover{background-color:transparent;color:#FB1D5E;transition:color .5s fade}nav.navbar ul.navbar-nav li a.active{background-color:transparent;color:#FB1D5E}nav.navbar .navbar-toggle .icon-bar{background-color:#FB1D5E}.cards h2.title{color:#0D0912;text-align:left}.cards a,.cards a:hover{text-decoration:none}.card .card-wrapper .c-title{color:#FB1D5E}.card .card-wrapper .c-content{color:#0D0912}.lists h2.title{color:#0D0912;text-align:left}.lists a,.lists a:hover{text-decoration:none}.list .list-wrapper .l-title{color:#FB1D5E}.list .list-wrapper .l-content{color:#0D0912}.list:nth-of-type(odd){background-color:#f4f4f4}
